import axios from 'axios';
import { message } from 'antd';

/**
 * get
 * @param url       
 * @param msg       
 * @param headers   header
 */
export const get = ({ url, msg = '', config }) =>
  axios
    .get(url, config)
    .then(res => res.data)
    .catch(err => {
      console.log(err);
      message.warn(msg);
    });

/**
 * post
 * @param url       
 * @param data      
 * @param msg       
 * @param headers   header
 */
export const post = ({ url, data, msg = '', config }) =>
  axios
    .post(url, data, config)
    .then(res => res.data)
    .then(data => {
      if (data.isSuccess) {
        return data.data;
      } else {
        message.warn(msg);
        return {};
      }
    })
    .catch(err => {
      console.log(err);
      message.warn(msg);
    });
